    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    transform: translateZ(0);

}

.upper-left-h4 {
    font-size: 14px;
    font-weight: 600;
    line-height: 1.4;
    text-transform: uppercase;
    letter-spacing: 4px;

}

.upper-left-h3 {
    font-family: var(--display-font-family);
    font-size: 24px;
    font-weight: 600;
    line-height: 1.4;
}

.left-left-item {
    margin: 20px 0 60px;
    padding-right: calc(.5 / var(--column-count) * (var(--inner-width) - (var(--gutter) * 2)));
}

.left-right-item {
    margin: 20px 0 60px;
    padding-right: calc(.5 / var(--column-count) * (var(--inner-width) - (var(--gutter) * 2)));
}

.left-left-item-name {
    margin-bottom: 8px;
    font-family: var(--display-font-family);
    font-size: 32px;
    font-weight: 800;
    line-height: 1.4;
}

.left-left-item-desc {
    font-weight: 600;
