.left-right-item-name {
    margin-bottom: 8px;
    font-family: var(--display-font-family);
    font-size: 32px;
    font-weight: 800;
    line-height: 1.4;
}

.left-right-item-desc {
    font-weight: 600;
    opacity: .5;
}

/* Section 4 */
.section4{
    margin-top: 150px;
}
.section4-heading {
    text-align: center;
    padding: 0.5em 0;
    font-family: var(--display-font-family);
    font-size: 100px;
    font-weight: 900;
    line-height: 1;
    letter-spacing: -.015em;
    background: url(https://d33wubrfki0l68.cloudfront.net/_next/static/media/f4f16a82938b3471164dab57854d8c5b36bff0ff/big-gradient-2.cec2615303f228afe4e48bc145bee6a7.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    transform: translateZ(0);
    margin-bottom: 83px;
}

.section4-heading-text {
    text-align: center;
    width: -moz-calc(4/var(--column-count) * (var(--inner-width) - (var(--gutter) * 2)));
    width: calc(4 / var(--column-count) * (var(--inner-width) - (var(--gutter) * 2)));
    margin: auto;
    font-family: var(--display-font-family);
    font-size: 24px;
    font-weight: 600;
    line-height: 1.4;
}

.section4-items {
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
    gap: 40px;
    margin-left: 350px;
    margin-right: 350px;
    margin-top: 100px;

}

.section4-item-kind {
    margin-bottom: 60px;
}

.section4-item-name {
    margin-bottom: 12px;
    font-family: var(--display-font-family);
    font-size: 32px;
    font-weight: 800;
    line-height: 1.4;
    margin-top: 42.5px;
}

.section4-item-desc {
    font-weight: 600;
    opacity: .5;
    max-width: 373px;
}

